<template>
  <div class="PersonManag">
    <topbar title="" color="#F83478"/>
    <cube-button @click.prevent="showBtn"></cube-button>
  </div>
</template>

<script>
import Topbar from '@/components/Topbar/Topbar'

export default {
  name: 'PersonManag',
  methods: {
    async loginOut () {
      this.$store.dispatch('reqLogout')
    },
    showBtn () {
      this.$createDialog({
        type: 'confirm',
        icon: 'cubeic-alert',
        title: '?',
        content: '',
        confirmBtn: {
          text: '',
          active: true,
          disabled: false,
          href: 'javascript:;'
        },
        cancelBtn: {
          text: '',
          active: false,
          disabled: false,
          href: 'javascript:;'
        },
        onConfirm: () => {
          this.loginOut()
          this.$createToast({
            type: 'warn',
            time: 1000,
            txt: ''
          }).show()
        },
        onCancel: () => {
          this.$createToast({
            type: 'warn',
            time: 1000,
            txt: ''
          }).show()
        }
      }).show()
    }
  },
  components: { Topbar }
}
</script>

<style scoped lang="scss">
  .PersonManag {
    padding-top: 43px;
  }
</style>
